export const NavigatorReducer = (state = {contentData: [0,0]}, action) => {
    switch (action.type) {
        case 'setContentData':
            return {contentData: action.data}
        default:
            return state;
    }
}

export function mapStateToProps(state) {
    return {
        contentData: state.navigation.contentData
    }
}

export function mapDispatchToProps(dispatch) {
    return {
        setContentData: (data) => dispatch({type: "setContentData", data: data})
    }
}