import React from 'react'
import CustomHeader from './CustomHeader'
import DottedCircle from './../../../dependencies/views/ux/DottedCircle';
import OyeJoysbrightPictures from '../../../dependencies/views/lov/OyeJoysbrightPictures';
import NameBox from '../../../dependencies/views/ux/NameBox';
import Quote, {Quoter} from './../../../dependencies/views/lov/Quote';
import ColorfulSplash from '../../../dependencies/views/ux/ColorfulSplash';
import FeaturedIn from './FeaturedIn';
import { Link } from 'react-router-dom';
import DetailCard from '../../../dependencies/views/layout/DetailCard';
import Footer from './Footer';
import { useEffect } from 'react';

var prevPosition = window.pageYOffset;
var counter = 1;
const animOptions = {
    duration: 5000,
    iterations: 1,
    fill: "forwards"
};

function Home() {
    

    useEffect(() => {
        window.onscroll = function () {
            var currentPosition = window.pageYOffset;
            const splashPos = 100;
            if (prevPosition > currentPosition) {
                document.getElementById("dotted-circle").animate([
                    {transform: `rotate(${counter*3}deg)`}
                ], animOptions);

                document.getElementById("left-splash").animate([
                    {transform: `translateX(-${splashPos}px)`}
                ], animOptions);

                document.getElementById("right-splash").animate([
                    {transform: `translateX(${splashPos}px)`}
                ], animOptions);
                counter++;
            } else {
                document.getElementById("dotted-circle").animate([
                    {transform: `rotate(${counter*-3}deg)`}
                ], animOptions);
                
                document.getElementById("left-splash").animate([
                    {transform: `translateX(${splashPos}px)`}
                ], animOptions);

                document.getElementById("right-splash").animate([
                    {transform: `translateX(-${splashPos}px)`}
                ], animOptions);
                counter--;
            }
            prevPosition = currentPosition;
            counter = counter < 1? 1 : counter;
        }
    });
    
    return (
        <div>
            <CustomHeader />
            <div className="jpc-hidden-mobile">
                <DottedCircle style={{backgroundSize: 'contain'}} id="dotted-circle" className="jpc-scroll-effect" width="400px" height="400px" position="absolute" top="100px" right="100px" />
                <OyeJoysbrightPictures style={{borderRadius: "100%"}} position="absolute" zIndex="-5" height="400px" width="400px" opacity="0.9" top="50px" right="0" />
                <NameBox position="absolute" title="Software Engineer" className="name-box" top="450px" right="70px" width="200px" />
            </div>
            <section>
                <div className="section-cover jpc-overflow-hidden-mobile">
                    <ColorfulSplash id="left-splash" position="absolute" width="512px" height="318px" top="0px" left="0px" opacity="0.1" />
                    <ColorfulSplash id="right-splash" position="absolute" width="512px" height="318px" bottom="0px" right="calc(100% - 1150px)" opacity="0.1" />
                    <Quote>
                        <Quoter content="Ayomide Joysbright Oyediran (Comrade)" />
                    </Quote>
                    <div className="get-started">
                        <p>
                            You need a software engineer to develop an application (be it website/webapp, desktop app or mobile app), or a network engineer to setup and network your ICT centre (be it office, school, hospital, etc), or you want to enhance your team
                            by putting them under training for more efficiency and capability? Then I am at your service, <b> HIRE </b> me.
                        </p>
                        <div className="t-right">
                            <Link to="hire" className="hire">HIRE ME</Link>
                        </div>
                    </div>
                </div>
                <FeaturedIn />
                <div className="jpc operations">
                {
                    operations.map(obj => <DetailCard key={obj.id} title={obj.title} content={obj.content} src={obj.src} />)
                }
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default Home

const operations = [
    {id: 1, title: "Web Development", content: <div><div>Do you have an idea to digitalize? I am here for you.</div> <Link className="hire" to="hire#engineer" >HIRE</Link> </div>, src: "./joysbright/web-development.jpg"},
    {id: 2, title: "Mobile Development", content: <div><div>Do you have an idea to digitalize? I am here for you.</div> <Link className="hire" to="hire#engineer" >HIRE</Link> </div>, src: "./joysbright/mobile-development.jpg"},
    {id: 3, title: "Desktop Development", content: <div><div>Do you have an idea to digitalize? I am here for you.</div> <Link className="hire" to="hire#engineer" >HIRE</Link> </div>, src: "./joysbright/desktop-development.png"},
    {id: 4, title: "Networking", content: <div><div>Do you have an idea to digitalize? I am here for you.</div> <Link className="hire" to="hire#engineer" >HIRE</Link> </div>, src: "./joysbright/networking.png"},
    {id: 5, title: "Training", content: <div><div>Do you have an idea to digitalize? I am here for you.</div> <Link className="hire" to="hire#engineer" >HIRE</Link> </div>, src: "./joysbright/training.png"},
]