import React from 'react'
import RadialDot, { RadialDotType } from '../../../dependencies/views/ux/RadialDot'

function Logo() {
    return (
        <div className="logo">
            AYOMIDE J.O
            <RadialDot position="absolute" opacity="0.2" left="50px" type={RadialDotType.Medium} />
        </div>
    )
}

export default Logo
