import React from 'react'
import { MdMail } from 'react-icons/md';
import { GrFacebookOption, GrGithub, GrInstagram, GrLinkedin, GrTwitter } from 'react-icons/gr';
import { CgMail } from 'react-icons/cg';
import { Link } from 'react-router-dom';
import ImageField from './../../../dependencies/views/ImageField';
import { FaNpm } from 'react-icons/fa';

function Footer() {

    const date = () => {
        const d = new Date().getFullYear();
        return d !== 2011? "- "+d : null;
    }
    
    return (
        <footer>
            <div className="jpc flexible-stack">
                <div className="stack-item" style={{width: "300px"}}>
                    <form className="form-block">
                        <h4>SIGN UP PROCESS INSIGHT</h4>
                        <p>Get the latest from Joysbright in your inbox. </p>
                        <ImageField src={<MdMail color="var(--black)" />} className="subscribe-input" onChange={() => {}} />
                        <p>I respect your privacy and I will never sell your data to third parties.</p>
                        <button className="subscribe-button">SUBSCRIBE NOW</button>
                    </form>
                </div>
                <div className="stack-item" style={{width: "300px"}}>
                    <h4>HIRING SERVICES</h4>
                    {
                        HIRING_SERVICES.map((obj, index) => (
                            <div className="link">
                                <span>◽</span>
                                <Link key={index} to={obj.href}>{obj.label}</Link>
                            </div>
                        ))
                    }
                </div>
                <div className="stack-item" style={{width: "300px"}}>
                    <h4>MOST RATED ARTICLES</h4>
                    {
                        MOST_RATED_ARTICLES.map((obj, index) => (
                            <div className="link">
                                <span>◽</span>
                                <Link key={index} to={obj.id}>{obj.subject}</Link>
                            </div>
                        ))
                    }
                </div>
                <div className="stack-item" style={{width: "320px"}}>
                    <h4>CONTACT</h4>
                    {
                        CONTACTS.map((obj, index) => (
                            <div className="link">
                                <span style={{color: "white"}}>{obj.src}</span>
                                <a className="social-media" target="_blank" rel="noreferrer" key={index} href={obj.href}>{obj.username}</a>
                            </div>
                        ))
                    }
                </div>
            </div>
            <div className="desc"> <em>(c) OyeJoysbright. 2011 {date()}</em></div>
        </footer>
    )
}

export default Footer

const HIRING_SERVICES = [
    {label: "Training", href: "/hire/trainer"},
    {label: "Software Engineering", href: "/hire/engineer"},
    {label: "Consultation", href: "/hire/consultant"},
    {label: "Motivation", href: "/hire/motivator"},
    {label: "Interview", href: "/hire/interviewer"}
]

const MOST_RATED_ARTICLES = [
    {id: 1, subject: "Public Dressing"},
    {id: 2, subject: "Who I am"},
    {id: 3, subject: "Becoming a developer"},
    {id: 4, subject: "Starting a new career"},
    {id: 5, subject: "Who I can become"}
]

const CONTACTS = [
    {src: <GrLinkedin />, username: "Ayomide Joysbright Oyediran", href: "https://www.linkedin.com/in/oyejoysbright"},
    {src: <GrGithub />, username: "OyeJoysbright", href: "https://www.github.com/oyejoysbright"},
    {src: <FaNpm />, username: "OyeJoysbright", href: "https://www.npmjs.com/~oyejoysbright"},
    {src: <CgMail />, username: "Oyediranjoysbright@gmail.com", href: "mailto:oyediranjoysbright@gmail.com"},
    {src: <GrFacebookOption />, username: "Ayomide Joysbright Oyediran", href: "https://www.facebook.com/OyeJoysbright"},
    {src: <GrInstagram />, username: "OyeJoysbright", href: "https://www.instagram.com/oyejoysbright"},
    {src: <GrTwitter />, username: "OyeJOysbright", href: "https://twitter.com/OyeJoysbright"}
]