import React from 'react'

function FeaturedIn() {
    return (
        <div className="featured-in">
            <h1>FEATURED IN</h1>
            <div className="jpc flexible-stack">
            {
                data.map((obj, index) => (
                    <div key={index} className="stack-item">
                        {obj.logo? <img alt="" key={obj.id} title={obj.title || obj.label} width="100px" height="80px" src={obj.logo} /> : <h3 key={obj.id} title={obj.title || obj.label}>{obj.label}</h3>}
                    </div>
                ))
            }
            </div>
        </div>
    )
}

export default FeaturedIn
const data = [
    {id: 1, label: "TAWOL Technologies Limited", logo: "./joysbright/tawol.png"},
    {id: 2, label: "Sterling Technologies and Data Sciences", logo: "./joysbright/SterlingTech.png"},
    {id: 3, label: "JPC", title: "Joysbright Programming Crew", logo: null},
    {id: 4, label: "HANSPET", logo: null},
    {id: 5, label: "KAYNETPC", logo: null},
    {id: 6, label: "FPA CICT", title: "The Federal Polytechnic Ado-Ekiti, CICT", logo: null},
]