import { DIALOG_ALERT, DIALOG_CONFIRM, DIALOG_HIDE } from '../../../dependencies/views/dialog/dialogActionReducers';
import { NavigatorReducer } from './NavigatorReducer';

const initState = {
    dialog: {
        type: DIALOG_HIDE,
        message: "",
        confirmAction: null
    },
    navigation: {
        contentData: [1,1]
    }
}

export const Reducers = (state = initState, action)=>{
    switch (action.type) {
    
        case DIALOG_ALERT:
        case DIALOG_CONFIRM:
        case DIALOG_HIDE:
            
            return Object.assign({}, state, {dialog: action});
        case 'setContentData': 
            return Object.assign({}, state, {navigation: NavigatorReducer(state.navigation, action)});
        default:
            return state;
    }
}